import {
	customer,
	customerPUT,
	createStill,
	findStillByLatAndLng
} from '../../api/index.js'
export default {
	data() {
		return {
			id: 0, // 使用 marker点击事件 需要填写id
			title: 'map',
			latitude: 0,
			longitude: 0,
			covers: [{
					latitude: 0,
					longitude: 0,
					callout: {content: "语言：珊珊是不是傻    \n    预计到达时间：10分钟    \n    车牌号:12345" ,
					 width: 35,   
					  height: 30,   
					 color: "#ff0000",  
					 fontSize: "16",   
					  borderRadius: "10",  
					 bgColor: "#ffffff",  
					 padding: "10",  
					 display:"ALWAYS" 
					}
					},
					
					],
				code: "",
				modalName: null,
				name: '',
				description: ''
			}
		},
		created() {
				const this_ = this
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						if (loginRes.errMsg == "login:ok") {
							const code = loginRes.code
							const data = {
								code: code
							}
							customer(data).then(ele => {
								if (ele.code != 7) {
									uni.setStorageSync('customer', ele.data)
								} else {
									uni.showToast({
										title: ele.msg,
										duration: 3000,
										icon: "none",
									})
								}
							})
						}
					}
				})
				//获取用户位置
				uni.getLocation({
					type: 'wgs84',
					success: function(res) {
						console.log('当前位置的经度：' + res.longitude);
						console.log('当前位置的纬度：' + res.latitude);
						this_.longitude = res.longitude
						this_.latitude = res.latitude
						this_.covers[0].latitude = res.latitude
						this_.covers[0].longitude = res.longitude
						const data = {
							lng: res.longitude,
							lat: res.latitude
						}
						findStillByLatAndLng(data).then(res => {
							const Arr = res.data
							Arr && Arr.map(item => {
								const obj = {}
								obj.latitude = item.lat
								obj.longitude = item.lng
								this_.covers.push(obj)
							})
						})
						console.log(this_.covers)
					}
				});

			},
			methods: {
				async test(e) {
					if (e.detail.errMsg = "getUserInfo:ok") {
						const data = {
							openid: uni.getStorageSync('customer').openid,
							rawData: e.detail.rawData,
							encryptedData: e.detail.encryptedData,
							iv: e.detail.iv,
							signature: e.detail.signature
						}
						const ele = await customerPUT(data)
						uni.removeStorage({
							key: 'customer'
						});
						uni.setStorageSync('customer', ele.data)
					}
				},
				showModal(e) {
					this.modalName = e.currentTarget.dataset.target
				},
				hideModal(e) {
					this.modalName = null
				},
				async submit() {
					const data = {
						exaCustomerId: uni.getStorageSync('customer').ID,
						name: this.name,
						description: this.description,
						lng: this.longitude,
						lat: this.latitude,
						open: true
					}
					const ele = await createStill(data)

					if (ele.code == 0) {
						this.hideModal()
						this.covers = []
						console.log(this.covers)
						const that = this
						uni.showToast({
							title: '出摊成功！',
							duration: 2000,
							icon: "none",
							success() {
								const datas = {
									lng: that.longitude,
									lat: that.latitude,
								}
								findStillByLatAndLng(datas).then(res => {
									const Arr = res.data
									Arr && Arr.map(item => {
										const obj = {}
										obj.latitude = item.lat
										obj.longitude = item.lng
										that.covers.push(obj)
									})
								})
							}
						})
						console.log(this.covers)
					} else {
						uni.showToast({
							title: ele.msg,
							duration: 3000,
							icon: "none",
						})
					}
				}
			}
	}
